import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Utente } from '../utenti/utente';
import { Ordini } from './ordini';

@Injectable({
  providedIn: 'root'
})
export class OrdiniService {

  constructor(private httpClient: HttpClient) { }

  public getListaOrdini(utn: Utente): Promise<Ordini[]> {
    return this.httpClient.get<Ordini[]>('http://localhost:8080/Store3D/chiamateRest/ordini/listaOrdini',
      this.httpOption(utn)).toPromise();
  }

  public getRemoveAllOrdini(utn: Utente): Promise<boolean> {
    return this.httpClient.get<boolean>('http://localhost:8080/Store3D/chiamateRest/ordini/removeAllOrdini',
      this.httpOption(utn)).toPromise();
  }

  private httpOption(ut: Utente) {
    const httpOptions = {
      headers: new HttpHeaders({
      })
      .append('username', ut.codicefiscale)
      .append('password' , ut.password)
    };
    return httpOptions;
  }
}
