import { CarrelloService } from './../../carrello/carrello.service';
import { LoginService } from './../../utenti/login.service';
import { CatalogoService } from 'src/app/catalogo/catalogo.service';
import { Component, OnInit } from '@angular/core';
import { Prodotto } from '../prodotto';
import { Router } from '@angular/router';
import { Carrello } from 'src/app/carrello/carrello';
import { TmplAstTemplate } from '@angular/compiler';
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-dettaglio-prodotto',
  templateUrl: './dettaglio-prodotto.component.html',
  styleUrls: ['./dettaglio-prodotto.component.css']
})
export class DettaglioProdottoComponent implements OnInit {

  constructor(private catService: CatalogoService, private router: Router,
              private utenteServ: LoginService,
              private carrelloServ: CarrelloService) { }
  prodotto: Prodotto;

  data: Date = new Date();
  loggedUt = this.utenteServ.utenteAtuale;
  counter = Array;
  qSelected: number;
  ngOnInit() {
    if (!this.catService.prodottoDettaglio) {
      this.router.navigate([('home')]);
    }
    this.prodotto = this.catService.prodottoDettaglio;
    if (this.prodotto.quantita > 0) {
      this.qSelected = 1;
    }
  }

  aggiungiAlCarrello() {
    const tmp = this.creaPrdCarrello();
    this.carrelloServ.postFindP(tmp, this.loggedUt)
      .then(
        response => {
          if (response === null) {
            this.addNew(tmp);
          }
          if (response !== null) {
            const x = +tmp.quantita_prodotto;
            const newQ = response.quantita_prodotto + x;
            if (newQ <= this.prodotto.quantita) {
              response.quantita_prodotto = newQ;
              this.updateExisted(response);
            }
          }
        }
      );
  }

  creaPrdCarrello(): Carrello {
    // tslint:disable-next-line:prefer-const
    let tmp: Carrello = new Carrello();
    tmp.id_cliente = this.loggedUt.codicefiscale;
    tmp.id_prodotto = this.prodotto.id;
    tmp.quantita_prodotto = this.qSelected;
    tmp.prezzo_prodotto = this.prodotto.prezzo;
    const d = new Date();
    tmp.data = d;
    return tmp;
  }

  addNew(ca: Carrello) {
    this.carrelloServ.postAddP(ca, this.loggedUt)
      .then(
        response => {
          if (response === true) {
            this.carrelloServ.getListCarrello(this.loggedUt)
              .then(
                res => {
                  let cont = 0;
                  for (const x of res) {
                    cont = cont + x.quantita_prodotto;
                  }
                  this.carrelloServ.quantitaCarrello = cont;
                  this.router.navigate([('carrello')]);
                }
              );
          } else {
            console.log('errore aggiunta carrello');
          }
        },
        error => {
          console.log('errore aggiunta carrello');
        }
      );
  }

  updateExisted(ca: Carrello) {
    this.carrelloServ.postEditP(ca, this.loggedUt)
      .then(
        response => {
          if (response === true) {
            this.carrelloServ.getListCarrello(this.loggedUt)
              .then(
                res => {
                  let cont = 0;
                  for (const x of res) {
                    cont = cont + x.quantita_prodotto;
                  }
                  this.carrelloServ.quantitaCarrello = cont;
                  this.router.navigate([('carrello')]);
                }
              );
          } else {
            console.log('errore modifica in  carrello');
          }
        },
        error => {
          console.log('errore modifica in  carrello');
        }
      );
  }

}
