import { Utente } from './../utenti/utente';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Carrello } from './carrello';

@Injectable({
  providedIn: 'root'
})
export class CarrelloService {

  constructor(private httpClient: HttpClient) { }

  public quantitaCarrello: number;
  public carrelloAttuale: Carrello[];
  public totaleCarrello: number;

  public postAddP(data: Carrello, utn: Utente): Promise<boolean> {
    return this.httpClient.post<boolean>('http://localhost:8080/Store3D/chiamateRest/carrello/aggiungiProdCarrello',
      data, this.httpOption(utn)).toPromise();
  }

  public postEditP(data: Carrello, utn: Utente): Promise<boolean> {
    return this.httpClient.post<boolean>('http://localhost:8080/Store3D/chiamateRest/carrello/modificaProdCarrello',
      data, this.httpOption(utn)).toPromise();
  }

  public postFindP(data: Carrello, utn: Utente): Promise<Carrello> {
    return this.httpClient.post<Carrello>('http://localhost:8080/Store3D/chiamateRest/carrello/findProdCarrello',
      data, this.httpOption(utn)).toPromise();
  }

  public postRemoveP(data: Carrello, utn: Utente): Promise<boolean> {
    return this.httpClient.post<boolean>('http://localhost:8080/Store3D/chiamateRest/carrello/removeProdCarrello',
      data, this.httpOption(utn)).toPromise();
  }

  public getListCarrello(utn: Utente): Promise<Carrello[]> {
    return this.httpClient.get<Carrello[]>('http://localhost:8080/Store3D/chiamateRest/carrello/caricaListaCarrello',
    this.httpOption(utn)).toPromise();
  }

  public postAcquista(data: Carrello[], utn: Utente, indirizzo: string): Promise<number> {
    return this.httpClient.post<number>('http://localhost:8080/Store3D/chiamateRest/prodotto/acquistaCarrello',
      data, this.httpOptionInd(utn, indirizzo)).toPromise();
  }

  public getRemoveAllCarrello(utn: Utente): Promise<boolean> {
    return this.httpClient.get<boolean>('http://localhost:8080/Store3D/chiamateRest/carrello/removeAllCarrello',
      this.httpOption(utn)).toPromise();
  }

  private httpOption(ut: Utente) {
    const httpOptions = {
      headers: new HttpHeaders({
      })
      .append('username', ut.codicefiscale)
      .append('password' , ut.password)
    };
    return httpOptions;
  }

  private httpOptionInd(ut: Utente, indirizzo: string) {
    const httpOptions = {
      headers: new HttpHeaders({
      })
      .append('username', ut.codicefiscale)
      .append('password' , ut.password)
      .append('indirizzo', indirizzo)
    };
    return httpOptions;
  }
}
