import { Carta } from './carta';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Utente } from '../utenti/utente';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CartaService {

  constructor(private httpClient: HttpClient) { }

  postAddCarta(data: Carta, utCorrente: Utente): Observable<boolean> {
    return this.httpClient.post<boolean>('http://localhost:8080/Store3D/chiamateRest/carta/addCarta',
      data , this.httpOption(utCorrente));
  }

  getListCarta(utCorrente: Utente): Observable<Carta[]> {
    return this.httpClient.get<Carta[]>('http://localhost:8080/Store3D/chiamateRest/carta/listaCarte',
      this.httpOption(utCorrente));
  }

  getLastIdCarta(utCorrente: Utente): Observable<number> {
    return this.httpClient.get<number>('http://localhost:8080/Store3D/chiamateRest/carta/lastIdCarta',
      this.httpOption(utCorrente));
  }

  private httpOption(ut: Utente) {
    const httpOptions = {
      headers: new HttpHeaders({
      })
      .append('cf', ut.codicefiscale)
      .append('password' , ut.password)
    };
    return httpOptions;
  }
}
