import { Prodotto } from './../catalogo/prodotto';
import { CarrelloService } from './../carrello/carrello.service';
import { CatalogoService } from './../catalogo/catalogo.service';
import { Component, OnInit } from '@angular/core';
import { LoginService } from '../utenti/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor( private loginService: LoginService, private router: Router,
               private catalogoService: CatalogoService,
               private caServ: CarrelloService) { }

  utCorrente = this.loginService.utenteAtuale;
  numProdotti = this.caServ.quantitaCarrello;
  keyFind = '';

  ngOnInit() {
  }

  public reidirectTo(cmp: string) {
    this.router.navigate([(cmp)]);
  }


  private clearTentativi() {
    this.loginService.clearTentativi();
  }

  private loadProdotti(f?) {
    this.catalogoService.caricaProdotti(f);
  }

  private searchProdotti(key, flag?) {
    console.log('da cercare', key);
    key = key.trim();
    const tmp = new Prodotto();
    tmp.nome = key;
    this.catalogoService.searchProdotti(tmp, flag);
  }

  logout() {
    this.loginService.eseguiLogout();
  }

  private reloadCatalogo(key?, flag?) {
    if (this.catalogoService.catalogoAttivo === true) {
      if (flag === true) {
        this.searchProdotti(key, false);
      } else {
        this.loadProdotti(false);
      }
    }
  }

}
