import { Amministratore } from './../amministratore/amministratore';
import { Prodotto } from './prodotto';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class CatalogoService {

  listaProdotti: Prodotto[];
  prodottoDettaglio: Prodotto;
  flagCambiamenti = false;
  catalogoAttivo = false;

  constructor(private httpClient: HttpClient,  private router: Router) { }

  public getCaricaProdotti(): Observable<Prodotto[]> {
    return this.httpClient.get<Prodotto[]>('http://localhost:8080/Store3D/chiamateRest/prodotto/caricaListaProdotti');
  }

  public postCercaProdotti(key: Prodotto): Promise<Prodotto[]> {
    return this.httpClient.post<Prodotto[]>('http://localhost:8080/Store3D/chiamateRest/prodotto/ricercaProdotti', key).toPromise();
  }

  public postAddEditProdotto(data, type, admin: Amministratore): Promise<boolean> {
    const httpOptions = {
      headers: new HttpHeaders({
      })
      .append('username', admin.codicefiscale)
      .append('password' , admin.password)
    };

    let url = '';
    if (type === 'add_operation') {
      url = 'http://localhost:8080/Store3D/chiamateRest/prodotto/aggiungiProdotto';
    } else if (type === 'edit_operation') {
      url = 'http://localhost:8080/Store3D/chiamateRest/prodotto/modificaProdotto';
    }
    return this.httpClient.post<boolean>(url,
      data, httpOptions).toPromise();
  }

  public getLastId(): Promise<number> {
    return this.httpClient.get<number>('http://localhost:8080/Store3D/chiamateRest/prodotto/ultimoId').toPromise();
  }

  public postDeleteProdotto(data, admin: Amministratore): Promise<boolean> {
    const httpOptions = {
      headers: new HttpHeaders({
      })
      .append('username', admin.codicefiscale)
      .append('password' , admin.password)
    };
    return this.httpClient.post<boolean>('http://localhost:8080/Store3D/chiamateRest/prodotto/eliminaProdotto', data, httpOptions)
    .toPromise();
  }

  public caricaProdotti(flag?) {
    console.log('flag reid', flag);

    this.getCaricaProdotti()
    .subscribe(result => {
      this.listaProdotti = [];
      for (const p of result) {
        if (p.quantita > -1) {
          this.listaProdotti = this.listaProdotti.concat(p);
        }
      }
      if (flag) {
        this.router.navigate([('catalogo')]);
      }
      if (flag === false) {
        this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
          this.router.navigate(['catalogo']);
        });
      }
    },
    error => {
      console.log('errore chiamata', error);

    }
    );
  }

  public searchProdotti(key: Prodotto, flag?) {

    this.postCercaProdotti(key)
    .then(result => {
      this.listaProdotti = [];
      for (const p of result) {
        if (p.quantita > -1) {
          this.listaProdotti = this.listaProdotti.concat(p);
        }
      }
      if (flag === true) {
        this.router.navigate([('catalogo')]);
      }
      if (flag === false) {
        this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
          this.router.navigate(['catalogo']);
        });
      }
    },
    error => {
      console.log('errore chiamata', error);

    }
    );
  }
}
