import { ControlloCampiService } from './../util/controllo-campi.service';
import { Prodotto } from './../catalogo/prodotto';
import { CatalogoService } from 'src/app/catalogo/catalogo.service';
import { OrdiniService } from './ordini.service';
import { Component, OnInit } from '@angular/core';
import { Ordini } from './ordini';
import { LoginService } from '../utenti/login.service';
import { DatePipe } from '@angular/common';
import { forkJoin } from 'rxjs';
import { Indirizzo } from '../util/indirizzo';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ordini',
  templateUrl: './ordini.component.html',
  styleUrls: ['./ordini.component.css']
})
export class OrdiniComponent implements OnInit {

  constructor(private ordiniService: OrdiniService, private router: Router,
              private logService: LoginService,
              private prodService: CatalogoService,
              private datepipe: DatePipe,
              private ctrlCampi: ControlloCampiService) { }
  listaOrdini: Ordini[];
  listaProdotti: Prodotto[];
  loaded = false;
  numeroOrdini = 0;
  ngOnInit() {
    if (!this.logService.utenteAtuale) {
      this.router.navigate([('home')]);
    }
    forkJoin(this.ordiniService.getListaOrdini(this.logService.utenteAtuale),
    this.prodService.getCaricaProdotti())
    .subscribe(
        res => {
          if (res[0] != null) {
            this.listaOrdini = res[0];
          }
          if (res[1] != null) {
            this.listaProdotti = res[1];
          }
          this.numeroOrdini = this.listaOrdini.length;
          console.log('numero ordini', this.numeroOrdini);
          this.loaded = true;
        }
      );
  }

  public prodottiToArray(prod: string): string[] {
    const list = prod.split(',');
    return list;
  }

  public getDate(data: Date): string {
/*     const dataFormatted = data.toString();
 */
    const dataFormatted = this.datepipe.transform(data, 'dd/MM/yyyy');
    return dataFormatted;
  }

  public decriptProdotto(idP: string): Prodotto {
    for (const x of this.listaProdotti) {
      if (Number(idP) === x.id) {
        return x;
      }
    }
    return null;
  }

  private decriptIndirizzo(ord: string): Indirizzo {
    return this.ctrlCampi.indirizzoFromString(ord);
  }
}
