import { CarrelloService } from './../../carrello/carrello.service';
import { LoginService } from '../login.service';
import { Component, OnInit, OnChanges } from '@angular/core';
import { CredenzialiLogin } from './login';
import { Router } from '@angular/router';

const ALERTS: Alert[] = [
  { // 0
    type: 'danger',
    message: 'Errore utente inesitente o password errata!'
  }
];

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {


  alerts: Alert[] = [];
  tipoCampo = 'password';
  constructor(
    public logServizi: LoginService,
    private router: Router,
    private carrelloServ: CarrelloService) { }
    ut = new CredenzialiLogin();


  public entra() {
    this.logServizi.eseguiLogin(this.ut);

  }
// gestione alert /////
  close(alert: Alert) {
    this.alerts.splice(this.alerts.indexOf(alert), 1);
  }

  addError(n: number) {
    const a = Array.from(ALERTS);
    this.alerts.push(a[n]);
  }
///////////////////////

public showPassword() {
  if (this.tipoCampo === 'password') {
    this.tipoCampo = 'email';
  } else {
    this.tipoCampo = 'password';
  }
}
  ngOnInit() {
    if (this.logServizi.tentativoLogin) {
      this.addError(0);
    }
  }


}
