import { Utente } from './../utente';
import { Component, OnInit } from '@angular/core';
import { LoginService } from '../login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-gestione-dati',
  templateUrl: './gestione-dati.component.html',
  styleUrls: ['./gestione-dati.component.css']
})
export class GestioneDatiComponent implements OnInit {

  constructor(private logService: LoginService, private router: Router) { }

  utenteAttivo: Utente;

  ngOnInit() {
    if (!this.logService.utenteAtuale || this.logService.utenteAtuale === null) {
      this.router.navigate([('home')]);
    }
    this.utenteAttivo = this.logService.utenteAtuale;

  }

}
