import { ControlloCampiService } from './../../util/controllo-campi.service';
import { Amministratore } from './../amministratore';
import { Component, OnInit } from '@angular/core';
import { LoginAdminService } from '../login-admin/login-admin.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'app-modifica-password-admin',
  templateUrl: './modifica-password-admin.component.html',
  styleUrls: ['./modifica-password-admin.component.css']
})
export class ModificaPasswordAdminComponent implements OnInit {

  confirmNewPassword;
  oldPassword;
  newPassword;
  flag = {
    fOld: true,
    fNew: true,
    fCNew: true
  };

  constructor(public logAdminService: LoginAdminService,
              private modalService: NgbModal,
              private router: Router,
              public ctrlCampi: ControlloCampiService) { }


  adminAttivo = this.logAdminService.loggedAdmin;
  risultatoModifica;
  ngOnInit() {
  }

  public ctrlCampo(): void {
    if (this.oldPassword) {
      if (this.adminAttivo.password === this.oldPassword) {
        this.flag.fOld = true;
      } else {
        this.flag.fOld = false;
      }
    }
    if (this.newPassword) {
      if ((this.ctrlCampi.checkPassword(this.newPassword)) && this.newPassword !== this.adminAttivo.password) {
        this.flag.fNew = true;
      } else {
        this.flag.fNew = false;
      }
    }
    if (this.confirmNewPassword) {
      if (this.ctrlCampi.controlloPassword(this.confirmNewPassword, this.newPassword)) {
        this.flag.fCNew = true;
      } else {
        this.flag.fCNew = false;
      }
    }
  }

  public salva(content): void {
    const copiaAdmin = Object.assign({}, this.adminAttivo);
    this.adminAttivo.password = this.newPassword;
    this.logAdminService.postUpdateData(this.adminAttivo, copiaAdmin)
    .then(
      response => {
        this.risultatoModifica = response;
        this.modalService.open(content);
      },
      error => {
        this.risultatoModifica = false;
        this.modalService.open(content);
      }
    );
  }

  public closeModale() {
    if (this.risultatoModifica) {
      this.logAdminService.logOutAdmin();
      this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
        this.router.navigate(['login_admin']);
     });
    }
    this.modalService.dismissAll();
  }

}
