import { Utente } from '../utente';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RegistrazioneService {

  constructor(private httpClient: HttpClient, private router: Router) { }


  postRegistrazione(data: Utente): Observable<Utente> {
    return this.httpClient.post<Utente>('http://localhost:8080/Store3D/chiamateRest/cliente/aggiungiCliente',
      data);
  }

  postRicercaEmail(data: Utente) {
    return this.httpClient.post('http://localhost:8080/Store3D/chiamateRest/cliente/cercaEmail',
      data).toPromise();
  }

  postRicercaCf(data: Utente) {
    return this.httpClient.post('http://localhost:8080/Store3D/chiamateRest/cliente/cercaCf',
      data).toPromise();
  }

  eseguiRegistrazione(dati: Utente, alerts, a) {
    this.postRegistrazione(dati)
      .subscribe(result => {
        if (result) {
          this.router.navigate([('home')]);
        }
      },
        error => {
          alerts.push(a[2]);
        }
      );
  }


  /* ricercaEmail(dati: Utente): Promise <boolean> {

    return new Promise(( resolve , reject ) =>  {
      this.postRicercaEmail(dati);

    });
  } */
}
