import { Amministratore } from './../amministratore';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';


@Injectable({
  providedIn: 'root'
})
export class LoginAdminService {

  loggedAdmin: Amministratore;
  tentativoLogin = false;

  constructor(private httpClient: HttpClient, private router: Router) { }

  public postLogin(data: Amministratore): Promise<Amministratore> {
    return this.httpClient.post<Amministratore>('http://localhost:8080/Store3D/chiamateRest/amministratore/eseguiLoginAmministratore',
      data).toPromise();
  }

  public postUpdateData(data, admO: Amministratore): Promise<boolean> {
    return this.httpClient.post<boolean>('http://localhost:8080/Store3D/chiamateRest/amministratore/updateAdmin',
    data, this.httpOption(admO)).toPromise();
  }

  public getListCf(admO: Amministratore): Promise<string[]> {
    return this.httpClient.get<string[]>('http://localhost:8080/Store3D/chiamateRest/amministratore/caricaListaAdmin',
    this.httpOption(admO)).toPromise();
  }

  public getListEmail(admO: Amministratore): Promise<string> {
    return this.httpClient.get<string>('http://localhost:8080/Store3D/chiamateRest/amministratore/caricaListaEmail',
    this.httpOption(admO)).toPromise();
  }

  public postAddNew(data, admO): Promise<boolean> {
    return this.httpClient.post<boolean>('http://localhost:8080/Store3D/chiamateRest/amministratore/addAdmin', data,
    this.httpOption(admO)).toPromise();
  }

  public logOutAdmin() {
    this.loggedAdmin = null;
    this.tentativoLogin = false;
  }

  private httpOption(admin: Amministratore) {
    const httpOptions = {
      headers: new HttpHeaders({
      })
      .append('username', admin.codicefiscale)
      .append('password' , admin.password)
    };
    return httpOptions;
  }
}
