import { GestioneDatiComponent } from './utenti/gestione-dati/gestione-dati.component';
import { AcquistoComponent } from './acquisto/acquisto.component';
import { DettaglioProdottoComponent } from './catalogo/dettaglio-prodotto/dettaglio-prodotto.component';
import { OrdiniComponent } from './ordini/ordini.component';
import { CarrelloComponent } from './carrello/carrello.component';
import { ModificaDatiComponent } from './utenti/gestione-dati/modifica-dati/modifica-dati.component';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { RegistrazioneComponent } from './utenti/registrazione/registrazione.component';
import { LoginComponent } from './utenti/login/login.component';

import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CatalogoComponent } from './catalogo/catalogo.component';
import { ModificaPasswordComponent } from './utenti/gestione-dati/modifica-password/modifica-password.component';
import { LoginAdminComponent } from './amministratore/login-admin/login-admin.component';
import { MetodiPagamentoComponent } from './footer/metodi-pagamento/metodi-pagamento.component';
import {ContattiComponent} from './footer/contatti/contatti.component';

const routes: Routes = [
{ path: '', component: HomeComponent },
{ path: 'login', component: LoginComponent },
{ path: 'registrazione', component: RegistrazioneComponent },
{ path: 'catalogo', component: CatalogoComponent },
{ path: 'appComponent', component: AppComponent},
{ path: 'login_admin', component: LoginAdminComponent},
{ path: 'carrello', component: CarrelloComponent},
{ path: 'ordini', component: OrdiniComponent},
{ path: 'dettaglioProdotto', component: DettaglioProdottoComponent},
{ path: 'acquisto', component: AcquistoComponent},
{ path: 'gestione_profilo', component: GestioneDatiComponent},
{ path: 'metodi_pagamento', component: MetodiPagamentoComponent},
{ path: 'contatti', component: ContattiComponent},
// otherwise redirect to home
{ path: '**', redirectTo: '' }];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
