import { CarrelloService } from './../carrello/carrello.service';
import { CredenzialiLogin } from './login/login';
import { Utente } from './utente';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';


@Injectable({
  providedIn: 'root'
})

export class LoginService {

  utenteAtuale: Utente;
  tentativoLogin = false;
  tentativoUpdate = false;
  aggiornamentoOk = false;

  error: any;

  constructor(private httpClient: HttpClient, private router: Router,
              private carServ: CarrelloService) { }

  postLogin(data: CredenzialiLogin): Observable<Utente> {
    return this.httpClient.post<Utente>('http://localhost:8080/Store3D/chiamateRest/cliente/eseguiLoginClienteEmail',
      data);
  }

  postUpdateUtente(data: Utente, utCorrente: Utente): Observable<Utente> {
    return this.httpClient.post<Utente>('http://localhost:8080/Store3D/chiamateRest/cliente/updateCliente',
      data , this.httpOption(utCorrente));
  }

  postDeleteUtente(data: Utente): Observable<boolean> {
    return this.httpClient.post<boolean>('http://localhost:8080/Store3D/chiamateRest/cliente/rimuoviCliente',
      data);
  }

  private ricarica(compNome) {
    // fa un refresh del component stesso
    this.router.navigateByUrl('/RefreshComponent', { skipLocationChange: true }).then(() => {
      this.router.navigate([compNome]);
   });
  }

  public eseguiLogin(dati: CredenzialiLogin) {
    this.postLogin(dati)
      .subscribe(result => {
        this.utenteAtuale = result;
        this.tentativoLogin = false;
        if (this.utenteAtuale && this.utenteAtuale !== null) {
          // richiama il servizio per caricare il carrello
          this.carServ.getListCarrello(this.utenteAtuale)
          .then(
            res => {
              this.carServ.carrelloAttuale = res;
              let cont = 0;
              for (const c of res) {
                cont = cont + c.quantita_prodotto;
              }
              this.carServ.quantitaCarrello = cont;
              this.router.navigate([('home')]);
            },
            err => {
              console.log('errore caricamento carrello');
            }
          );
        } else {
          this.tentativoLogin = true;
          this.ricarica('login');
        }
        console.log('stato utente sub=  ' + this.utenteAtuale);
      },
      error => {
        console.log('errore chiamata', error);
        this.tentativoLogin = true;
        this.ricarica('login');
      }

      );
  }

  public eseguiLogout() {
    this.utenteAtuale = null;
    this.clearTentativi();
    this.router.navigate([('login')]);
  }

  public eseguiUpdate(dati: Utente, utCor: Utente) {
    this.postUpdateUtente(dati, utCor)
      .subscribe(result => {
        this.utenteAtuale = result;
        this.tentativoUpdate = false;
        this.aggiornamentoOk = true;
        this.ricarica('modifica_password');
        console.log('stato aggiornamento=  ' + this.utenteAtuale);
      },
      error => {
        console.log('errore chiamata', error);
        this.tentativoUpdate = true;
        this.ricarica('modifica_password');

      }
      );
  }

  public clearTentativi() {
    this.tentativoLogin = false;
    this.tentativoUpdate = false;
    this.aggiornamentoOk = false;
  }

  private httpOption(ut: Utente) {
    const httpOptions = {
      headers: new HttpHeaders({
      })
      .append('cf', ut.codicefiscale)
      .append('password' , ut.password)
    };
    return httpOptions;
  }
}
